.fav-button{
  background: rgba(255, 172, 172, .3);
  border: 0;
  border-radius: 100px;
  cursor: pointer;
  width: 30px;
  height: 30px;
  margin: 0 4px;
  transition: all .3s ease;
}

.fav-button:hover {
  background: rgba(255, 172, 172, .8);
}

.fav-button span{
  display: block;
  width: 20px;
}